button {
	border: none;
	background: transparent;
}

button:focus {
	outline-color: transparent;
}

a {
	text-decoration: none;
	color: inherit;
}

.cta {
	position: relative;
	margin: auto;
	padding: 1.3rem 1.5rem;
	transition: all 0.2s ease;
}

.cta:before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	display: block;
	border-radius: 35px;
	background: rgba(0, 255, 70, 0.5);
	width: 4rem;
	height: 4rem;
	transition: all 0.3s ease;
}

.cta span {
	color: white;
	position: relative;
	font-size: 16px;
	line-height: 18px;
	font-weight: 900;
	letter-spacing: 0.25em;
	text-transform: uppercase;
	vertical-align: middle;
}

.cta svg {
	position: relative;
	top: 0;
	margin-left: 1em;
	fill: none;
	stroke-linecap: butt;
	stroke-linejoin: miter;
	stroke: #fff;
	stroke-width: 2;
	transform: translateX(-0.5em);
	transition: all 0.3s ease;
}

.cta:hover > #label {
	color: black;
}

.cta:hover:before {
	width: 100%;
	background: rgb(0, 255, 70);
	cursor: pointer;
}

.cta:hover svg {
	transform: translateX(0);
	stroke: #000;
}

.cta:active {
	transform: scale(0.96);
}